import React from 'react'
import Header from '../components/Header'
import Footer from '../components/Footer'
import '../styles/about.scss';

const About = () => {
    return (
        <div>
        <Header />
        <div id='body'>
            <div className='textTitle'>
            <h2>About Page</h2>
            </div>
            <div className='textBody'>
            This blog site is simply created to demonstrate my skills and technical know-how of the usage of react js and its open-source library.
            </div>
        </div>
        <Footer />   
        </div>
    )
}

export default About
