import React from 'react'
import Header from '../components/Header'
import Footer from '../components/Footer'
import { Link } from 'react-router-dom'
import '../styles/index.scss';

const Index = () => {
    return (
        <div>
        <Header />
        <div id='body'>
            <div className='textTitle'>
            <h2>Welcome to my first project!</h2>
            </div>
            <div className='textBody'>
            <a>Please see the </a><Link to='/about'> About </Link> <a>section to learn more of this site</a> <br/>
            <a>or visit the </a><Link to='/contactus'> Contact </Link> <a>section if you want to reach me or see more of my other projects.</a> <br/>
            <a>go to the </a><Link to='/blog'> Blog </Link> <a>section to do more testing</a>
            </div>
        </div>
        <Footer />
        </div>
    )
}

export default Index

